@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282C34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  color: lightgray;
}

.App{text-align:left;padding:20px}.won-card{background-color:#4DA375}.lost-card{background-color:#A34D5D}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 5fr}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px}

.YearSelector{text-align:right;list-style:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.HomePage .app-name{font-size:3em;margin:20px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px}

